import * as React from "react"
import { graphql } from "gatsby"

import Layout from "../components/layout"
import Seo from "../components/seo"
import ImageBackground from "../images/images_homenowords-02.jpg"
import { makeStyles } from "@material-ui/core"
import FlickerEffect from "../components/flickerEffect"
const useStyles = makeStyles(() => ({
   image: {
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
      '& div': {
         backgroundImage: `url(${ImageBackground})`,
         backgroundSize: '100% auto',
         backgroundRepeat: 'no-repeat',
         backgroundPosition: 'center',
         display: 'flex',
         justifyContent: 'center',
         alignItems: 'center',
         position:'relative',
         "@media (max-width: 767px)": {
            height: '25vh',
            width: '95vw',
         },
         "@media (min-width: 768px) and (max-width: 1023px)": {
            height: '33vh',
            width: '90vw',
         },
         "@media (min-width: 1024px) and (max-width: 1280px)": {
            height: '35vw',
            width: '70vw',
         },
         "@media (min-width: 1281px) and (max-width: 1439px)": {
            height: '50vh',
            width: '50vw',
         },
         "@media (min-width: 1440px)": {
            height: '48vh',
            width: '45vw'
         },
      }
   },
   configArtical: {
      textAlign: 'justify',
      '& ul':{
         paddingLeft:40,
         '& li':{
            margin:0
         }
      }
   },
   dynamicContent: {
      position: 'absolute',
      borderTop: '1px solid white',
      borderBottom: '1px solid white',
      textTransform: 'uppercase',
      color: "white",
      padding: '5px 0',
      textAlign: 'center',
      "@media (max-width: 767px)": {
         width: '60vw',
         top: '52%',
         left: '9%'
      },
      "@media (min-width: 768px) and (max-width: 1023px)": {
         width: '47vw',
         top: '70%',
         left: '13%'
      },
      "@media (min-width: 1024px) and (max-width: 1280px)": {
         width: '40vw',
         top: '69%',
         left: '8%'
      },
      "@media (min-width: 1281px) and (max-width: 1439px)": {
         width: '26vw',
         top: '64%',
         left: '12%'
      },
      "@media (min-width: 1440px)": {
         width: '26vw',
         top: '68%',
         left: '12%'
      },
   },
   article:{
      '& h1':{
         textAlign: 'left', 
         fontSize:'1.7rem', 
         color:'black', 
         margin:'20px 0 10px',
         fontWeight:'400'
      },
      '& h6':{
         textAlign: 'left', 
         fontSize:'1.4rem', 
         color:'black', 
         margin:'20px 0 10px',
         fontWeight:'400'
      }
   }
}));

const listWordWhatWeDo = ['BALANCED', 'MODERNIZED', 'UNCLOUDED', 'ENJOYED', 'LIVED', 'SIMPLIFIED']

const WhatWeDo = ({ data, location }) => {
   const classes = useStyles()
   const siteTitle = data.site.siteMetadata?.title || `Title`

   return (
      <Layout location={location} title={siteTitle}>
         <Seo title="What We Do" description="offer software development services" />
         <div>
            <div className={classes.image}>
               <div>
                  <p className={classes.dynamicContent}>
                     A
                     <FlickerEffect listWord={listWordWhatWeDo}></FlickerEffect>
                     APPROACH TO TECHNOLOGY
                  </p>
               </div>
            </div>
            <article className={classes.article}>
               <h1>What We Do</h1>
               <h6>We offer software development services</h6>
               <div className={classes.configArtical}>We make your ideas come to life by helping you create web and mobile applications on time and on budget.
               We do this while focusing on professionalism and relationships so that we can make a bigger difference.
               <ul>
                     <li>We ask lots of questions to find out the "whys" of your product ideas so that we can partner with you and contribute ideas, shortcuts, and creativity.</li>
                     <li>You will see where we are in the development process frequently. We work hard to eliminate big surprises.</li>
                     <li>You get to work with a smart, dedicated team of people that are trained to communicate and collaborate effectively with you.</li>
                     <li>We deliver projects up to 50% faster because we have a 24-7 development cycle.</li>
                  </ul>
               </div>

               <h6>We make products</h6>
               <p className={classes.configArtical}>We are working on some cool projects that we hope will simplify life for the users,
               do what they are supposed to do well, and get out of the way when they are not truly needed.</p>
            </article>
         </div>
      </Layout>
   )
}

export default WhatWeDo

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }) {
      nodes {
        excerpt
        fields {
          slug
        }
        frontmatter {
          date(formatString: "MMMM DD, YYYY")
          title
          description
        }
      }
    }
  }
`
